'use strict';

import '../style/app.scss'

import React from 'react'
import ReactDom from 'react-dom'
import {Router,Route,hashHistory,IndexRoute} from 'react-router'

import Tabs from './components/tabs'
import Index from './components/index'
import Guang from './components/guang'
import List from './components/list'
import Me from './components/me'
import ShopCat from './components/shopcat'
import Details from './components/details.jsx'

import TabBarActions from './flux/actions/TabBarActions'


let onEnterHandler = (type) => {
  TabBarActions.setItem(type)
}

ReactDom.render(
  (
    <Router history={hashHistory}>
	    	<Route path='/' component={Tabs}>
          <IndexRoute onEnter={onEnterHandler.bind(this,'index')} component={Index}></IndexRoute>
          <Route path='index' onEnter={onEnterHandler.bind(this,'index')} component={Index}></Route>
          <Route path='guang' onEnter={onEnterHandler.bind(this,'guang')} component={Guang}></Route>
          <Route path='list' onEnter={onEnterHandler.bind(this,'list')} component={List}></Route>
          <Route path='me' onEnter={onEnterHandler.bind(this,'me')} component={Me}></Route>
          <Route path='shopcat' onEnter={onEnterHandler.bind(this,'shopcat')} component={ShopCat}></Route>
        </Route>
        <Route path ='/details/:id' component={Details}></Route>
    </Router>
  ),
  document.getElementById('app')
)



