'use strict';

import React from 'react'
import {Link} from 'react-router'
import Scroller from '../../Yo-dev-v3/component_dev/scroller/src/index.js'

class Index extends React.Component{

	constructor(props){
		super(props)
		this.state = {
			featuresDate:[]
		}
	}

	setNodes(callback){
		let that = this
		fetch('/api/features.php').
		then(response => response.json()).
		then(res => {
			// console.log(res);
			// console.log(res[0]);
			// console.log(res[0].data);
			// console.log(res[0].data.list);
			// console.log(res[0].data.list[0]);
			// console.log(res[0].data.list[0].src);
			let data = res[0].data.list.map((val,key) => {
				return(
					<img src={val.src} />
				)
			})
			that.setState({
				featuresDate:data
			})
		})
	}

	 render(){
        return (
            <div className='m-index2'>
							<header>
								<ul>
									<li className='yo-ico'>&#xe603;</li>
									<li><img src='yohologo02.png' /></li>
									<li className='yo-ico'>&#xe601;</li>
								</ul>
							</header>
							<Scroller>
								<div>
									{this.state.featuresDate}
								</div>
							</Scroller>
						</div>
        )
    }

		componentDidMount(){
			this.setNodes.call(this)
			// this.setNodes()
			// this.setState({
			// 	featuresDate:this.setNodes()
			// })
		}
}

export default Index



