'use strict';

import React from 'react'

class Details extends React.Component{

  constructor(props) {
      super(props)
      this.state={
          details:[]
      }
  }

  getNodes(url,id,callback){
    fetch(url)
      .then(response => response.json())
      .then(res => {
        console.log(res)
      })
  }

  render(){
    return(
      <div>
        <header>abc</header>
        {this.state.details}
      </div>
    )
  }

  componentDidMount(){
    this.getNodes.call(this,'/api/details.php',65093,(data) => {
      this.setState({
        details:data
      })
    })
  }
}

export  default Details



