'use strict';

import React from 'react'
import Carousel from '../../Yo-dev-v3/component_dev/carousel/src/index.js'
import List from '../../Yo-dev-v3/component_dev/list/src/index.js'


class Guang extends React.Component{

	constructor(props) {
        super(props)
        this.state={
            dataSource: [
							{}
						]
        }
    }

	setNodes(url,callback){
		fetch(url)
			.then(response => response.json())
			.then(res =>{
				// let data = res.artList.map((val,key)=>{
				// 	return(
				// 		<div>
				// 			<i><img src={val.img}/></i>
				// 		</div>
				// 	)
				// })
			callback(res.artList)
			})
	}



	render(){
		return(
			<div className='m-guang'>
				<header>
					<ul>
						<li>推荐</li>
						<li>话题</li>
						<li>搭配</li>
						<li>潮人</li>
						<li>潮品</li>
						<li>专题</li>
					</ul>
				</header>
				<List
				ref='list'
				staticSection={
					<Carousel>
					<li className="item"><img className="img" src="//img11.static.yhbimg.com/yhb-img01/2017/03/09/15/01dcf10dae3d588c562d6a6514845fe36e.jpg"/></li>
					<li className="item"><img className="img" src="//img11.static.yhbimg.com/yhb-img01/2017/03/09/14/0111a7c00c1f9306ca10606fa7080cfa5c.jpg"/></li>
					<li className="item"><img className="img" src="//img10.static.yhbimg.com/yhb-img01/2017/03/03/12/01c6e64236bd715c64c10cd56c59c4dbf4.jpg" /></li>
					<li className="item"><img className="img" src="//img11.static.yhbimg.com/yhb-img01/2016/07/11/17/01c7c796c808e0f7f88d638fe111997983.jpg?imageView2/2/w/640/h/275/q/60" /></li>
					<li className="item"><img className="img" src="//img11.static.yhbimg.com/yhb-img01/2017/03/17/11/011e7f37ad19ca731b74b447d8304cbc33.jpg" /></li>
				</Carousel>
			}
				dataSource={this.state.dataSource}
		    renderItem={(item,i)=>{
		        return (
		          <div>
								<div>
									<img src={item.clientimg} />
									<div>{item.clientname}</div>
								</div>
								<div className='subtitle'>{item.subtitle}</div>
								<img src={item.img}/>
								<div className='tl'>
									<b>{item.title}</b>
									<p>{item.describe}</p>
									<div  className='td'>
										<i className='yo-ico'>&#xe60b;</i>{item.publish_time}
										<i className='yo-ico'>&#xe63a;</i>{item.views_num}
										<i className='yo-ico'>&#xe645;</i>{item.praise_num}
									</div>
								</div>
							</div>
		        );
    		}
			}
			usePullRefresh={true}
        onRefresh={() => {
          	this.setNodes.call(this,'/api/guang2.php',(data)=>{
							this.setState({
								dataSource:data.concat(this.state.dataSource)
							})
							this.refs.list.stopRefreshing(true)
						})
        	}
				}
			useLoadMore={true}
         onLoad={() => {
					 this.setNodes.call(this,'/api/guang3.php',(data)=>{
						this.setState({
							dataSource:this.state.dataSource.concat(data)
						})
						this.refs.list.stopLoading(true)
					})
        }
			}

/>
			</div>
		)
	}

	componentDidMount(){
		this.setNodes.call(this,'/api/guang.php',(data)=>{
				this.setState({
					dataSource:data
				})
		})
	}

}

export default Guang



