'use strict';

import React from 'react'
import {Link} from 'react-router'

import TabBarStore from '../flux/stores/TabBarStore'
import TabBarActions from '../flux/actions/TabBarActions'

class Tabs extends React.Component {

  setTabBars(){
    let tabBars = [
           {
               title: "首页",
               icon: "&#xe600;",
               "chooseIcon": "&#xe62c;",
               type: "index"
           },
           {
               title: "分类",
               icon: "&#xe64a;",
               "chooseIcon": "&#xe64b;",
               type: "list"
           },
           {
               title: "逛",
               icon: "&#xe612;",
               "chooseIcon": "&#xe61f;",
               type: "guang"
           },
           {
               title: "购物车",
               icon: "&#xe610;",
               "chooseIcon": "&#xe60e;",
               type: "shopcat"
           },
           {
               title: "我的",
               icon: "&#xe66e;",
               "chooseIcon": "&#xe61b;",
               type: "me"
           }
       ];
       return tabBars.map((val,key) => {
         return(
             <li onClick={TabBarActions.setItem.bind(this,val.type)}
              className={val.type == TabBarStore.getItem() ? 'active' :''}>
              <Link to={val.type}>
                <i className='yo-ico' dangerouslySetInnerHTML={{__html : val.type ==TabBarStore.getItem() ? val.chooseIcon : val.icon}}></i>
                <b>{val.title}</b>
              </Link>
             </li>
         )
       })
  }

  // changeTabBar(type){
  //   this.setState({
  //     currentType:type
  //   })
  // }

  render(){
    return(
      <div className='m-index'>
        <section>
          {this.props.children}
        </section>
        <nav>
          <ul>
            {this.setTabBars()}
          </ul>
        </nav>
      </div>
    )
  }
}

export default Tabs



